let AppBrandingMixin = {
  methods: {
    loginLogo () {
      if (this.$store.state.branding.loginLogoAvailable) {
        let baseUrl = process.env.NODE_ENV === 'development' ? 'http://localhost:8080' : ''
        let appId = this.$store.getters.appId
        return `${baseUrl}/apps/Connect/DownloadPublic/downloadAppBrandingLoginLogo?appId=${appId}`
      } else {
        return require('@/assets/images/logo/login-logo.png')
      }
    },
    loginBackground () {
      if (this.$store.state.branding.loginBackgroundAvailable) {
        let baseUrl = process.env.NODE_ENV === 'development' ? 'http://localhost:8080' : ''
        let appId = this.$store.getters.appId
        return `${baseUrl}/apps/Connect/DownloadPublic/downloadAppBrandingLoginBackground?appId=${appId}`
      } else {
        return require('@/assets/images/pages/background.jpg')
      }
    },
    loginBackgroundPosition () {
      return this.$store.state.branding.loginBackgroundPosition
    },
    logo () {
      if (this.$store.state.branding.logoAvailable) {
        let baseUrl = process.env.NODE_ENV === 'development' ? 'http://localhost:8080' : ''
        let appId = this.$store.getters.appId
        return `${baseUrl}/apps/Connect/DownloadPublic/downloadAppBrandingLogo?appId=${appId}`
      } else {
        return require('@/assets/images/logo/logo.png')
      }
    },
    logoWidth () {
      return (this.$store.state.branding.loginLogoSize || 200) + 'px'
    }
  }
}

export default AppBrandingMixin
